<template>
  <div class="file-loader-page">
        <el-row class="file-loader-header">
            <el-col>
                <el-steps :active="inputComplete" align-center>
                    <el-step :title="$t('type')==='English'? 'Step 1' : '1'"
                             :description="$t('stepFileDes.stepOne')"
                    ></el-step>
                    <el-step :title="$t('type')==='English'? 'Step 2' : '2'"
                             :description="$t('stepFileDes.stepTwo')"
                    ></el-step>
                    <el-step :title="$t('type')==='English'? 'Step 3' : '3'"
                             :description="$t('stepFileDes.stepThree')"
                    ></el-step>
                    <el-step :title="$t('type')==='English'? 'Step 4' : '4'"
                             :description="$t('stepFileDes.stepFour')"
                    ></el-step>
                </el-steps>
            </el-col>
        </el-row>
        <el-row class="file-loader-container">
            <el-col class="file-upload">
                <div style="width: 400px; margin: auto;">
                    <el-upload
                            class="upload-demo"
                            action="/"
                            drag
                            :auto-upload="false"
                            multiple
                            :on-change="handleChange"
                            :before-remove="beforeRemove"
                            v-loading="loading"
                            :file-list="fileList">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">
                            {{this.$t('type') === 'English' ?
                            'Drag files here, or' :
                            ''}}
                            <em>{{this.$t('type') === 'English' ?
                                'click to select' :
                                ''}}</em>
                        </div>
                        <div class="el-upload__tip" slot="tip">
                            {{this.$t('type') === 'English' ?
                            'Only text files can be uploaded and no more than 1M' :
                            '1M'}}
                        </div>
                    </el-upload>
                    <div style="display:inline-block; margin-top: 18px;">
                        <el-button type="primary"
                                round
                                size="medium"
                                @click="fileConfirmed">
                            {{this.$t('type') === 'English' ?
                            'Submit' :
                            ''}}
                        </el-button>
                    </div>
                </div>
            </el-col>
            <el-col class="code-wrapper"
                    v-if="fileUploadComplete"
                    v-loading="isSubmitting">
                <div class="border-shadow" style="width: 65%; margin: 0 auto 18px;">
                    <codemirror v-model="code"
                                :options="cmOption"></codemirror>
                </div>
                <div style="text-align: center;">
                    <el-button round
                            type="primary"
                            size="medium"
                            @click="uploadConfirmed">
                        {{this.$t('type') === 'English' ?
                        'Confirm' :
                        ''}}
                    </el-button>
                </div>
            </el-col>
            <el-col class="file-result border-shadow" v-if="result.length > 0">
                <div class="title">
                    {{this.$t('result')}}
                </div>
                <div class="result">
                    <el-table :data="result"
                              style="width: 100%"
                              :row-class-name="tableRowClassName">
                        <el-table-column
                                type="index"
                                width="50">
                        </el-table-column>
                        <el-table-column
                                :label="this.$t('trace')"
                        >
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <el-tag class="tag-font"
                                            color="#FFFFFF"
                                            :class="scope.row.split('=')[1].trim() === '-1.0' ?
                                            redFont : ''"
                                    >{{
                                        scope.row.split('=')[0].trim()
                                        }}</el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                                :label="this.$t('value')"
                        >
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <div :class="scope.row.split('=')[1].trim() === '-1.0' ?
                                     redFont : ''">{{
                                        scope.row.split('=')[1].trim() === '-1.0' ?
                                        'Deadlock' : scope.row.split('=')[1].trim()
                                        }}</div>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="average" v-if="confirmAverage">
                    {{this.$t('type') === 'English' ?
                    'Calculate the average?' : ''}}
                    <span style="display: inline-block;margin-left: 10px;" >
                        <el-button
                                size="small"
                                round
                                @click="calculateAverage">
                            {{this.$t('type') === 'English' ? 'Yes' : ''}}
                        </el-button>
                        <el-button
                                type="info"
                                size="small"
                                round
                                @click="denyAverage">
                            {{this.$t('type') === 'English' ? 'No' : ''}}
                        </el-button>
                    </span>
                </div>
            </el-col>
        </el-row>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { codemirror } from 'vue-codemirror-lite'

require('codemirror/mode/python/python')
require('codemirror/mode/vue/vue')
require('codemirror/addon/selection/active-line.js')
require('codemirror/addon/edit/closebrackets.js')
require('codemirror/mode/clike/clike.js')
require('codemirror/addon/edit/matchbrackets.js')
require('codemirror/addon/comment/comment.js')
require('codemirror/addon/dialog/dialog.js')
require('codemirror/addon/dialog/dialog.css')
require('codemirror/addon/search/searchcursor.js')
require('codemirror/addon/search/search.js')
require('codemirror/keymap/emacs.js')

export default {
  name: 'FileLoader',
  components: {
    codemirror
  },
  data () {
    return {
      inputComplete: 0,
      fileList: [], // 
      loading: false, // 
      fileUploadComplete: false, // 
      code: '', // 
      cmOption: { // codemirror
        autoCloseBrackets: true,
        tabSize: 4,
        styleActiveLine: true,
        lineNumbers: true,
        line: true,
        mode: 'text/x-python',
        theme: 'base16-light',
        keyMap: 'emacs'
      },
      result: [], // 
      average: 0, // 
      confirmAverage: true,
      isSubmitting: false, // 
      redFont: 'red-font'
    }
  },
  methods: {
    /**
     * 
     * @param file
     * @returns {Promise<MessageBoxData> | *}
     */
    beforeRemove (file) {
      return this.$confirm(`${this.$t('confirmDelete')} ${file.name}`,
        this.$t('confirmTitle'),
        {
          confirmButtonText: this.$t('confirm'),
          cancelButtonText: this.$t('cancel'),
          type: 'warning'
        })
    },
    /**
     * 
     * @param file
     * @param fileList
     */
    handleChange (file, fileList) {
      this.fileList = fileList.slice(-1)
    },
    /**
     * 
     */
    fileConfirmed () {
      this.loading = true
      this.inputComplete = 1
      // 
      this.result = []
      this.average = 0
      this.confirmAverage = true
      if (this.fileList && this.fileList.length > 0) {
        for (let i = 0; i < this.fileList.length; i += 1) {
          const file = this.fileList[i]
          if (!file) {
            this.$message({
              type: 'warning',
              message: this.$t('checkFileType')
            })
            return
          }
          const reader = new FileReader()
          reader.onload = async (e) => {
            try {
              const document = e.target.result
              setTimeout(() => {
                this.loading = false
                this.fileUploadComplete = true
                this.inputComplete = 2
              }, 500)
              this.code = document
            } catch (err) {
              console.log(`${this.$t('fileLoadError')}${err.message}`)
              this.$message.error(`${this.$t('fileLoadError')}${err.message}`)
            }
          }
          reader.readAsText(file.raw)
        }
      } else {
        this.$message({
          type: 'warning',
          message: this.$t('fileLoadError')
        })
        this.loading = false
        this.inputComplete = 0
      }
    },
    /**
     * 
     */
    uploadConfirmed () {
      this.inputComplete = 3
      this.isSubmitting = true
      axios
        .post('Servlet', qs.stringify({
          code: this.code
        }))
        .then((response) => {
          this.average = response.data.data.average
          setTimeout(() => {
            this.result = response.data.data.result
            this.inputComplete = 4
            this.isSubmitting = false
          }, 500)
          console.log('', this.result)
        })
        .catch((response) => {
          this.$message.error(this.$t('calculateError'))
          this.isSubmitting = false
          console.log(response)
        })
    },
    /**
     * 
     */
    calculateAverage () {
      const average = `average = ${this.average}`
      this.result.push(average)
      this.confirmAverage = false
    },
    /**
     * 
     */
    denyAverage () {
      this.confirmAverage = false
    }
  }
}
</script>
<style scoped lang="stylus">
.file-loader-page
    position absolute
    top 20px
    bottom 20px
    left 20px
    right 20px

    .file-loader-header
        background-color #ffffff
        border-radius 8px
        padding 15px 0
        border 1px solid #c8ebdf

    .file-loader-container
        border-radius 8px
        background #ffffff
        border 1px solid #c8ebdf
        padding 20px 3px
        position absolute
        width 100%
        top 120px
