            overflow hidden

    .file-result
        margin 18px 0
        padding 15px

    .title
        padding-bottom 10px
        border-bottom 1px solid #E3E3E3
        font-weight bold

    .average
        margin-top 18px
        font-size 14px

    .tag-font
        color #333333
        font-size 18px
